.sidenav-cont {
    border-top: 3px solid #D10024;
    box-shadow: #6b7696 0 0 3px 0;
}

@media screen and (max-width: 990px) {
    .sidenav {
        height: 100%;
        width: 0;
        position: fixed;
        z-index: 10;
        top: 0;
        left: 0;
        background-color: #15161D;
        overflow-x: hidden;
        padding-top: 60px;
        transition: 0.5s;
    }

    #modal {
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        z-index: 1;
        background-color: rgba(0, 0, 0, 0.4);
    }

    .sidenav a {
        padding: 8px 8px 8px 32px;
        text-decoration: none;
        display: block;
        transition: 0.3s;
    }

    .sidenav .closebtn {
        position: absolute;
        top: 0;
        right: 25px;
        font-size: 36px;
        margin-left: 50px;
    }

    .nav-link-custom {
        border-bottom: #15161D 2px solid;
    }
}

.nav-link-custom:hover, .nav-link-custom:active {
    border-bottom: #D10024 2px solid;
    -webkit-transition: 0.2s all;
    transition: 0.2s all;
}

@media screen and (min-width: 991px) {
    .sidenav-cont {
        padding-top: 15px !important;
        padding-bottom: 15px !important;
    }

    .nav-item-custom {
        display: initial;
    }

    .closebtn {
        display: none;
    }

    .nav-link-custom {
        color: black;
    }
}

@media screen and (max-height: 450px) {
    .sidenav {
        padding-top: 15px;
    }

    .sidenav a {
        font-size: 18px;
    }
}