.explore-card-hover{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.explore-card-hover:hover{
    background-color: rgba(0, 0, 0, 0.4);
    transition: 0.3s;
}

.explore-card-hover:hover>p {
    display: block !important;
    margin-top: 25%;
    margin-left: 30px;
    font-size: 25px;
    font-weight: bolder;
    color: white;
    transition: 0.3s;
}